body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
    overflow: hidden;
margin-top: 250px;
    margin-left: 100px !important;
}
.logo1{
    margin-top: 100px;
    width: 80%;
    object-fit: contain;
    -webkit-animation: move 3s infinite ease alternate;
            animation: move 3s infinite ease alternate;
  
}
@-webkit-keyframes move{
    from{
        -webkit-transform: translateY(-24px);
                transform: translateY(-24px);
        -webkit-transform: translateX(-24px);
                transform: translateX(-24px);
    
    }
    to{
        -webkit-transform: translateY(0px) scale(2.04);
                transform: translateY(0px) scale(2.04);
        -webkit-transform: translateX(0px) scale(1.03);
                transform: translateX(0px) scale(1.03);
      
    }
    
}
@keyframes move{
    from{
        -webkit-transform: translateY(-24px);
                transform: translateY(-24px);
        -webkit-transform: translateX(-24px);
                transform: translateX(-24px);
    
    }
    to{
        -webkit-transform: translateY(0px) scale(2.04);
                transform: translateY(0px) scale(2.04);
        -webkit-transform: translateX(0px) scale(1.03);
                transform: translateX(0px) scale(1.03);
      
    }
    
}
