*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.logo{
    width: 300px;
    margin-top: 20px;
    margin-left: 190px;
    padding-bottom: 25px;
}
.navbar{
    position:fixed !important;
    z-index: 1;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height:120px;
    background-color:#fff;
    color: white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}
.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 60%;
    margin-top: 45px;
    margin-right: 50px;
    padding-bottom: 29px;
}
.home,
.about,
.contact,
.project {
    font-weight: bolder;
    font-size: 20px;
    text-decoration: none;
    color:#000;
    padding: 10px 20px;
    margin: 0 10px;
    cursor: pointer;
   
}
.mobile-menu-icons{
    display: none;
}
.nav-links hr{
  display: none;
}
/* -----------small devices----------------- */

@media screen and (max-width:1355px){
      .logo{
        width: 30%;
        margin-top: 30px;
        margin-left: 10px;
      } 

    .nav-links{
        display: none;
    }
    .nav-links-mobile{

        position: absolute;
        display: block;
        list-style: none;
        background-color:#fff;
        left: 0;
        top: 80px;
        transition: all 0.5s ease-out;
        width: 100%;
